import { defineCustomElement as defineCustomElementIcon } from '@ionic/core/components/ion-icon';
import { defineCustomElement as defineCustomElementItem } from '@ionic/core/components/ion-item';
import { defineCustomElement as defineCustomElementText } from '@ionic/core/components/ion-text';
import { withActions } from '@storybook/addon-actions/decorator';
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import { TpIonText } from 'src/components/ionic/text/utils/text.type';
import { TpIonItem } from '../../../../components/ionic/item/utils/item.type';
import { TpContextMenu } from '../context-menu';
import { TpContextMenuType } from '../utils/context-menu.type';

defineCustomElementText();
defineCustomElementIcon();
defineCustomElementItem();

const meta: Meta<TpContextMenu & TpContextMenuType & TpIonItem & TpIonText> = {
  component: 'TpContextMenu',
  title: 'components/Core/Context menu',
  decorators: [withActions as any],
  parameters: {
    actions: {
      handles: ['tpCollapse'],
    },
  },
  argTypes: {
    initialCollapsedState: {
      control: { type: 'boolean' },
      description: 'Define o estato inicial do componente.',
      table: {
        defaultValue: {
          summary: true,
        },
        type: {
          summary: 'boolean',
        },
      },
    },
  },
};

export default meta;

type Story = StoryObj<
  TpContextMenu & TpContextMenuType & TpIonItem & TpIonText
>;

export const Default: Story = {
  render: ({ ...args }) => {
    return html`
      <div class="markup-context-menu">
        <!-- component markdown -->

        <tp-context-menu
          color=${args.color}
          initial-collapsed-state="${args.initialCollapsedState}"
        >
          <ion-item slot="item" tp-spacingx="s00" tp-spacingy="s00">
            <ion-icon
              slot="start"
              class="tp-context-menu__icon"
              src="./assets/tp-star.svg"
            ></ion-icon>
            <ion-text tp-type="p16">Context Menu Item</ion-text>
          </ion-item>

          <ion-item slot="item" tp-spacingx="s00" tp-spacingy="s00">
            <ion-icon
              slot="start"
              class="tp-context-menu__icon"
              src="./assets/tp-star.svg"
            ></ion-icon>
            <ion-text tp-type="p16">Context Menu Item</ion-text>
          </ion-item>

          <ion-item slot="item" tp-spacingx="s00" tp-spacingy="s00">
            <ion-icon
              slot="start"
              class="tp-context-menu__icon"
              src="./assets/tp-star.svg"
            ></ion-icon>
            <ion-text tp-type="p16">Context Menu Item</ion-text>
          </ion-item>

          <ion-item slot="item" tp-spacingx="s00" tp-spacingy="s00">
            <ion-icon
              slot="start"
              class="tp-context-menu__icon"
              src="./assets/tp-star.svg"
            ></ion-icon>
            <ion-text tp-type="p16">Context Menu Item</ion-text>
          </ion-item>

          <ion-item slot="item" tp-spacingx="s00" tp-spacingy="s00">
            <ion-icon
              slot="start"
              class="tp-context-menu__icon"
              src="./assets/tp-star.svg"
            ></ion-icon>
            <ion-text tp-type="p16">Context Menu Item</ion-text>
          </ion-item>

          <ion-text slot="info" tp-type="p16b" color="neutral-8">
            Criado em 22/01/2024
          </ion-text>
        </tp-context-menu>

        <!-- !component markdown -->
      </div>
    `;
  },
};
